:root {
  --font-family-main: "DM Sans", sans-serif;
  --font-family-title: "Titan One", cursive;
  --cl-txt-main: #000;
  --cl-txt-second: #907e82;
  --cl-txt-white: #fff;
  --cl-txt-light: #e1e1e1;
  --cl-txt-accent-light: #ffa5ba;
  --cl-txt-accent-dark: #d41443;
  --bgc-main: #fff;
  --bgc-second: #fafafa;
  --bgc-menu: #e17992;
  --bgc-accent-ice: #fff5f6;
  --bgc-accent-cream: #ffb8ca;
  --bgc-accent-coffee: #f0d1a5;
  --bgc-accent-shake: #c2e297;
  --bgc-hover: #ecebeb;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --slow-anim: 200ms ease;
  --fast-anim: 400ms ease-in-out;
}

body {
  font-family: var(--font-family-main);
  letter-spacing: .04em;
  color: var(--cl-txt-main);
  background-color: var(--bgc-main);
  font-size: 14px;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  transition: color var(--anim);
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--cl-txt-accent-dark);
}

ul {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.section {
  width: 100%;
  margin: 0 auto;
  padding: 60px 0;
}

.uppertitle {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-light);
  align-items: center;
  margin-bottom: 15px;
  font-size: 30px;
  line-height: 1.13;
  display: flex;
}

.section-title {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-dark);
  font-size: 34px;
  line-height: 1.15;
}

.description {
  letter-spacing: .02em;
  margin-bottom: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
}

.no-scroll {
  overflow: hidden;
}

.js-no-transparency {
  transition: background-color var(--slow-anim), backdrop-filter var(--slow-anim);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(225, 121, 145, .4);
}

.btn {
  min-width: 160px;
  max-width: 190px;
  height: 44px;
  cursor: pointer;
  color: var(--cl-txt-accent-dark);
  background-color: var(--cl-txt-white);
  text-align: center;
  transition: color var(--anim), background-color var(--anim);
  border: none;
  border-radius: 22px;
  outline: none;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.btn:hover, .btn:focus {
  background-color: var(--bgc-hover);
}

.btn:after {
  content: "";
  width: 9px;
  height: 9px;
  fill: var(--cl-txt-accent-dark);
  background-image: url("pointer.0c6901df.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 13px;
  display: inline-block;
}

.btn:after:hover, .btn:after:focus {
  color: var(--cl-txt-white);
  background-color: var(--cl-txt-accent-dark);
}

.social {
  flex-direction: row;
  display: flex;
}

.social__item + .social__item {
  margin-left: 28px;
}

.social__item {
  width: 34px;
  height: 34px;
}

.social__icon {
  max-width: 19px;
  max-height: 19px;
  fill: var(--cl-txt-white);
}

.social__link {
  height: 100%;
  background-color: var(--cl-txt-accent-light);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social__link:hover, .social__link:focus {
  background-color: var(--cl-txt-accent-dark);
  outline: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header__mobile-menu {
  z-index: 11;
  width: 250px;
  height: 100vh;
  transition: transform var(--slow-anim);
  background-color: var(--bgc-menu);
  visibility: hidden;
  flex-direction: column;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: scroll;
  transform: translateX(100%);
}

.header__mobile-menu.is-open {
  visibility: visible;
  transition: transform var(--slow-anim);
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .header__mobile-menu {
    width: 255px;
    padding-top: 84px;
  }
}

@media screen and (min-width: 1200px) {
  .header__mobile-menu {
    display: none;
  }
}

.header__mobile-menu .btn {
  margin-bottom: 32px;
}

.mobile-btn {
  z-index: 11;
  width: 20px;
  height: 20px;
  color: var(--cl-txt-accent-dark);
  cursor: pointer;
  transition: color var(--anim);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-btn:hover, .mobile-btn:focus {
  color: var(--cl-txt-white);
}

.mobile-btn.is-open {
  position: fixed;
  overflow-y: scroll;
}

@media screen and (min-width: 768px) {
  .mobile-btn {
    top: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-btn {
    display: none;
  }
}

.mobile-btn__icon {
  fill: currentColor;
}

.mobile-btn__icon-open {
  display: block;
}

.mobile-btn.is-open .mobile-btn__icon-open, .mobile-btn__icon-close {
  display: none;
}

.mobile-btn.is-open .mobile-btn__icon-close {
  display: block;
  position: fixed;
}

.mobile-menu__navigation .navigation__list {
  display: inline-block;
}

.mobile-menu__navigation .navigation__item {
  margin-right: 0;
}

.mobile-menu__navigation .navigation__item:not(:last-child) {
  margin-bottom: 24px;
}

.mobile-menu__navigation .navigation__link {
  transition: color var(--anim);
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: flex;
  position: relative;
}

.mobile-menu__navigation .navigation__link:after {
  content: " ";
  height: 1px;
  width: 250px;
  background-color: #e18298;
  position: absolute;
  bottom: -11px;
  left: -20px;
}

.mobile-menu__navigation .navigation__link--current {
  color: var(--cl-txt-accent-dark);
}

.mobile-menu__navigation .navigation__link--current:before {
  content: " ";
  height: 1px;
  width: 250px;
  background-color: #e18298;
  position: absolute;
  top: -11px;
  left: -20px;
}

.header {
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
}

.header.js-no-transparency {
  background-color: rgba(255, 184, 202, .75);
}

.header.js-no-transparency .mobile-btn {
  top: 20px;
}

@media screen and (min-width: 768px) {
  .header.js-no-transparency .mobile-btn {
    top: 40px;
  }
}

.header__container {
