@keyframes shake-top {
  0%, 100% {
    transform-origin: 50% 0;
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(2deg);
  }

  20%, 40%, 60% {
    transform: rotate(-4deg);
  }

  30%, 50%, 70% {
    transform: rotate(4deg);
  }

  80% {
    transform: rotate(-2deg);
  }

  90% {
    transform: rotate(2deg);
  }
